<template>
  <section>
    <h1 style="text-align: center;">ENTRE EM CONTATO</h1>
    <form @submit.prevent="">
      <p style="text-align: center;"><b>{{ infoMessage }}</b></p>
      <label>Nome</label>
      <input v-model="name" type="text">
      <label>E-mail</label>
      <input v-model="email" type="email">
      <label>Como deseja enviar esta mensagem?</label>
      <select v-model="sendWith" required>
        <option>Selecione uma opção</option>
        <option value="selfNumber">Enviar a partir do meu whatsapp</option>
        <option value="byForm">Enviar via formulário e aguardar contato</option>
      </select>
      <label v-if="this.sendWith === 'byForm'">Telefone / Celular</label>
      <input v-if="this.sendWith === 'byForm'" v-model="phone" type="tel">
      <label>Qual serviço gostaria de solicitar?</label>
      <select v-model="service">
        <option>Selecione uma opção</option>
        <option value="Servidores Particulares">Servidores particulares</option>
        <option value="Telefonia Voip">Telefonia Voip</option>
        <option value="Monitoramento de ativos">Monitoramento de ativos</option>
        <option value="Desenvolvimento de sistemas">Desenvolvimento de sistemas</option>
        <option value="Sistema de multi-atendimento whatsapp (chatbot)">Sistema de multi-atendimento whatsapp (chatbot)
        </option>
        <option value="Suporte Técnico">Suporte Técnico</option>
        <option value="Outro">Outro</option>
      </select>
      <label v-if="this.service === 'Outro'">Digite o serviço que gostaria de solicitar</label>
      <textarea v-if="this.service === 'Outro'" v-model="customService" type="tel"></textarea>
      <button @click="submitForm">Enviar</button>
    </form>
  </section>
</template>

<script>
import axios from 'axios';

export default {
  name: 'FormOrcamentoComponent',
  data() {
    return {
      name: null,
      email: null,
      sendWith: null,
      phone: null,
      service: null,
      customService: null,
      infoMessage: null
    }
  },
  methods: {
    submitForm() {
      const messageApi = `
Mensagem recebida através do formulário de serviços do site

Nome: ${this.name}
E-mail: ${this.email}
Telefone: ${this.phone}
Serviço solicitado: ${this.service}
${this.customService ? 'Servico Personalizado: ' + this.customService : ''}
      `;

      const messageWhatsWeb = `
Olá, gostaria de solicitar um serviço! %0A%0A
Meu nome é: ${this.name}%0A
Meu e-mail é: ${this.email}%0A
Tenho interesse em: ${this.service}%0A
${this.customService ? 'Servico Personalizado: ' + this.customService : ''}
      `

      if (this.sendWith === 'byForm') {
        this.sendMessageWithApi(messageApi)
      } else if (this.sendWith === 'selfNumber') {
        this.sendMessageWithWhatsWeb(messageWhatsWeb)
      }

    },
    sendMessageWithApi(message) {
      axios.post('https://soluciona.inf.br/api/sendEvol.php', {
        message: message
      }, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .then((response) => {
          console.log(response)
          this.infoMessage = 'Mensagem enviada com sucesso, logo entraremos em contato!'
        })
        .catch(error => {
          console.error('Erro na requisição:', error);
        });

    },
    sendMessageWithWhatsWeb(message) {
      window.open(`https://wa.me/5511939282555?text=${message}`, "_blank")
    }
  }
}
</script>

<style scoped>
section {
  margin: 100px auto;
}
form {
  display: flex;
  flex-direction: column;
  margin: auto 20%;
}

form label {
  font-weight: bold;
}

form input,
form textarea,
form select {
  border-radius: 5px;
  border: 1px solid rgb(187, 177, 177);
  margin-bottom: 10px;
}

button {
  background-color: #E7CD56;
  border: 1px solid #E7CD56;
  padding: 5px;
  font-weight: bold;
  cursor: pointer
}

@media (max-width: 608px){
  form {
    margin: auto 10%;
  }
}
</style>
